import { pluginName } from './constants';

const CKEDITOR = window.CKEDITOR;

CKEDITOR.plugins.add(pluginName, {
  requires: 'richcombo',
  init(editor) {
    const colors = CKEDITOR.tools.get_plugin_config(pluginName, editor);

    /**
     * Wrap NODE with every element in parentChain so that
     * <strong><em>text</em></strong>  +  <span>  ⇒  <span><strong><em>text</em></strong></span>
     */
    function wrapInside(node, parentChain) {
      return parentChain.reduceRight((inner, outer) => {
        const clone = new CKEDITOR.dom.element(outer.getName());
        Object.entries(outer.getAttributes()).forEach(([k, v]) => clone.setAttribute(k, v));
        clone.append(inner);
        return clone;
      }, node);
    }

    /**
     * CKEditor applies colour by inserting <span style="color"> **inside** inline tags.
     * Move such inner spans so they wrap the tag instead.
     */
    /**
     * Move any colour <span> that CKEditor inserts **inside** inline tags so the
     * span wraps the tag instead – regardless of the exact colour value.
     */
    function liftColorSpans(range) {
      const r = range.clone();
      r.enlarge(CKEDITOR.ENLARGE_INLINE);
      const walker = new CKEDITOR.dom.walker(r);
      walker.evaluator = node =>
        node?.type === CKEDITOR.NODE_ELEMENT &&
        node.getName() === 'span' &&
        node.getStyle('color');   // any colour span

      let span;
      while ((span = walker.next())) {
        const parent = span.getParent();
        if (!parent || parent.getName() === 'span') continue; // already outer‑most
        span.remove();
        parent.insertBeforeMe(span); // move span before <strong>/<em>/…
        span.append(parent);         // span now wraps the formatting tag
      }
    }

    /**
     * Remove only colour from selection, preserve other inline styles.
     */
    function removeColor(range) {
      const w = new CKEDITOR.dom.walker(range.clone());
      w.evaluator = n => n.type === CKEDITOR.NODE_TEXT && n.getAscendant('span', true)?.getStyle('color');
      let text;
      while ((text = w.next())) {
        const span = text.getAscendant('span', true);
        if (!span) continue;

        const start = text.equals(range.startContainer) ? range.startOffset : 0;
        const end = text.equals(range.endContainer) ? range.endOffset : text.getLength();
        const before = text.substring(0, start);
        const mid = text.substring(start, end);
        const after = text.substring(end);

        const chain = [];
        let p = text.getParent();
        while (p && !p.equals(span)) { chain.unshift(p); p = p.getParent(); }

        const makeFrag = (content, keepColor) => {
          if (!content) return null;
          const base = wrapInside(new CKEDITOR.dom.text(content), chain);
          if (!keepColor) return base;
          const col = new CKEDITOR.dom.element('span');
          col.setAttribute('style', span.getAttribute('style'));
          col.append(base);
          return col;
        };

        [makeFrag(before, true), wrapInside(new CKEDITOR.dom.text(mid), chain), makeFrag(after, true)]
          .filter(Boolean)
          .forEach(frag => span.insertBeforeMe(frag));

        text.remove();
        if (!span.getChildCount()) span.remove();
      }
    }

    /* ---------- UI combo ---------- */
    editor.ui.addRichCombo('ApplyColor', {
      label: 'Apply Color',
      toolbar: 'styles',
      panel: {
        css: [CKEDITOR.skin.getPath('editor')].concat(editor.config.contentsCss),
        multiSelect: false
      },
      init() {
        this.add('default', 'Remove Color', 'Remove Color');
        Object.entries(colors).forEach(([label, val]) => this.add(val, label, label));
      },
      onRender() { this.setValue(''); },

      onClick(value) {
        editor.focus();
        const sel = editor.getSelection();
        if (!sel) return;
        const ranges = sel.getRanges();
        editor.fire('lockSnapshot');

        if (value === 'default') {
          ranges.forEach(r => removeColor(r));
        } else {
          const style = new CKEDITOR.style({ element: 'span', styles: { color: value } });
          editor.applyStyle(style);
          ranges.forEach(r => liftColorSpans(r, value));
        }

        editor.fire('unlockSnapshot');
      }
    });
  }
});
